.nav{
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    
    height: 30px;

    z-index: 1;
    display: flex;
    justify-content: space-between;

    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav_black{
    background-color: #000;

}

.nav_logo{
    position: fixed;
    padding-top: 0;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.nav_avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}